import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { SiteService } from '../../../services/site.service';
import { Main } from '../../../models/main';
import { Site } from '../../../models/site';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {
  id: string;
  main: Main = {
    company_name: '',
    main_address: '',
    main_address_2: '',
    main_city: '',
    main_state: '',
    main_zip: '',
    main_phone: '',
    main_email: '',
    main_country: '',
    main_contact_email: '',
    main_contact: '',
    main_contact_phone: '',
    main_hr_contact: '',
    main_hr_contact_email: '',
    main_hr_contact_phone: '',
    shipping_address: '',
    shipping_address_2: '',
    shipping_city: '',
    shipping_state: '',
    shipping_zip: '',
    shipping_country: '',
    shipping_phone: '',
    shipping_email: '',
    shipping_contact: ''
  };
  sites: Site[];

  constructor(
    private ss: SiteService,
    private router: Router,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params.id;
    this.ss.getMain(this.id).subscribe(mains => this.main = mains);
    this.ss.getSites().subscribe(sites => {
      this.sites = sites;
    });
  }

  onSubmit({value, valid}: {value: Main, valid: boolean}) {
    if (!valid) {
      console.log('Not valid');
    } else {
      value.id = this.id;
      this.ss.updateMain(value);
      this.router.navigate(['/company']);
    }
  }

}
