import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { SiteService } from '../../../../services/site.service';
import { JobService } from '../../../../services/job.service';

import { Site } from '../../../../models/site';
import { Job } from '../../../../models/job';

@Component({
  selector: 'app-list-jobs',
  templateUrl: './list-jobs.component.html',
  styleUrls: ['./list-jobs.component.css']
})
export class ListJobsComponent implements OnInit {
  id: string;
  site: Site = {
    id: '',
    site_name: '',
    site_address: '',
    site_city: '',
    site_state: '',
    site_zip: '',
    site_phone: '',
    site_email: '',
    site_country: '',
    site_contact_email: '',
    site_contact_first_name: '',
    site_contact_last_name: '',
    site_contact_phone: '',
    site_maintenance_contact: '',
    site_maintenance_email: '',
    site_maintenance_phone: '',
    site_ship_to_address: '',
    site_ship_to_city: '',
    site_ship_to_state: '',
    site_ship_to_zip: '',
    site_ship_to_country: '',
    site_ship_to_phone: '',
    site_ship_to_email: ''
  };
  jobId: string;
  job: Job = {
    id: '',
    job_title:  '',
    job_description: '',
    job_asset: '',
    job_notes: '',
    job_status: '',
    job_type: '',
    job_worker: '',
    site_id: '',
    worker_status: '',
    job_number: ''
  };
  jobs: Job[];

  constructor(private ss: SiteService, private js: JobService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.id = this.route.snapshot.params.id;
    this.ss.getSite(this.id).subscribe(site => {
      this.site = site;
    });
    this.ss.getJobs().subscribe(jobs => {
      this.jobs = jobs;
    });
  }

}
