import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { SiteService } from '../../../services/site.service';

import { Site } from '../../../models/site';

@Component({
  selector: 'app-site-edit',
  templateUrl: './site-edit.component.html',
  styleUrls: ['./site-edit.component.css']
})
export class SiteEditComponent implements OnInit {
  id: string;
  site: Site = {
    id: '',
    site_name: '',
    site_address: '',
    site_address_2: '',
    site_city: '',
    site_state: '',
    site_zip: '',
    site_phone: '',
    site_email: '',
    site_country: '',
    site_contact_email: '',
    site_contact: '',
    site_contact_first_name: '',
    site_contact_last_name: '',
    site_contact_phone: '',
    site_maintenance_contact: '',
    site_maintenance_email: '',
    site_maintenance_phone: '',
    site_ship_to_address: '',
    site_ship_to_address_2: '',
    site_ship_to_city: '',
    site_ship_to_state: '',
    site_ship_to_zip: '',
    site_ship_to_country: '',
    site_ship_to_phone: '',
    site_ship_to_email: ''
  };

  constructor(private ss: SiteService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.id = this.route.snapshot.params.id;
    this.ss.getSite(this.id).subscribe(site => {
      this.site = site;
    });
  }

  onSubmit({value, valid}: {value: Site, valid: boolean}) {
    if (!valid) {
      console.log('Not valid');
    } else {
      value.id = this.id;
      this.ss.updateSite(value);
      this.router.navigate([`/company/site/${this.id}`]);
    }
  }

}
