import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Site } from '../models/site';
import { Main } from '../models/main';
import { Job } from '../models/job';

@Injectable({
  providedIn: 'root'
})

export class SiteService {
  siteCollection: AngularFirestoreCollection<Site>;
  siteDoc: AngularFirestoreDocument<Site>;
  sites: Observable<Site[]>;
  site: Observable<Site>;
  mainCollection: AngularFirestoreCollection<Main>;
  mainDoc: AngularFirestoreDocument<Main>;
  mains: Observable<Main[]>;
  main: Observable<Main>;
  jobCollection: AngularFirestoreCollection<Job>;
  jobDoc: AngularFirestoreDocument<Job>;
  jobs: Observable<Job[]>;
  job: Observable<Job>;

  constructor(private afs: AngularFirestore) {
    this.siteCollection = this.afs.collection('site_locations', ref => ref.orderBy('site_name'));
    this.mainCollection = this.afs.collection('main_company', ref => ref.orderBy('company_name'));
    this.jobCollection = this.afs.collection('site_jobs', ref => ref.orderBy('job_title'));
  }

  getSites(): Observable<Site[]> {
    this.sites = this.siteCollection.snapshotChanges().pipe(map(actions => {
      return actions.map(action => {
        const data = action.payload.doc.data() as Site;
        const id = action.payload.doc.id;
        return { id, ...data };
      });
    }));

    return this.sites;
}

getMains(): Observable<Main[]> {
  this.mains = this.mainCollection.snapshotChanges().pipe(map(actions => {
    return actions.map(action => {
      const data = action.payload.doc.data() as Main;
      const id = action.payload.doc.id;
      return { id, ...data };
    });
  }));

  return this.mains;

}

getJobs(): Observable<Job[]> {
  this.jobs = this.jobCollection.snapshotChanges().pipe(map(actions => {
    return actions.map(action => {
      const data = action.payload.doc.data() as Job;
      const id = action.payload.doc.id;
      return { id, ...data };
    });
  }));

  return this.jobs;
}

getMain(id: string): Observable<Main> {
  this.mainDoc = this.afs.doc<Main>(`main_company/${id}`);
  this.main = this.mainDoc.snapshotChanges().pipe(map(action => {
    if (action.payload.exists === false) {
      return null;
    } else {
      const data = action.payload.data() as Main;
      data.id = action.payload.id;
      return data;
    }
  }));

  return this.main;
}

getSite(id: string): Observable<Site> {
  this.siteDoc = this.afs.doc<Site>(`site_locations/${id}`);
  this.site = this.siteDoc.snapshotChanges().pipe(map(action => {
    if (action.payload.exists === false) {
      return null;
    } else {
      const data = action.payload.data() as Site;
      data.id = action.payload.id;
      return data;
    }
  }));

  return this.site;
}

updateMain(main: Main) {
  this.mainDoc = this.afs.doc(`main_company/${main.id}`);
  this.mainDoc.update(main);
}

updateSite(site: Site) {
  this.siteDoc = this.afs.doc(`site_locations/${site.id}`);
  this.siteDoc.update(site);
}

}
