import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { DetailsComponent } from './company/main/details/details.component';
import { EditComponent } from './company/main/edit/edit.component';
import { SiteDetailsComponent } from './company/site/details/site-details.component';
import { SiteEditComponent } from './company/site/edit/site-edit.component';
import { JobAddComponent } from './company/site/jobs/add/job-add.component';
import { EditJobComponent } from './company/site/jobs/edit/edit-job.component';
import { ShowJobComponent } from './company/site/jobs/show/show-job.component';
import { ListJobsComponent } from './company/site/jobs/lists/list-jobs.component';


const routes: Routes = [
  { path: 'company', component: DetailsComponent },
  { path: 'company/edit/:id', component: EditComponent },
  { path: 'company/site/:id', component: SiteDetailsComponent },
  { path: 'company/site/edit/:id', component: SiteEditComponent },
  { path: 'company/site/job/add/:id', component: JobAddComponent },
  { path: 'company/site/job/edit/:id', component: EditJobComponent },
  { path: 'company/site/job/show/:id', component: ShowJobComponent },
  { path: 'company/site/job/list/:id', component: ListJobsComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
