import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { SiteService } from '../../../../services/site.service';
import { JobService } from '../../../../services/job.service';

import { Site } from '../../../../models/site';
import { Job } from '../../../../models/job';

@Component({
  selector: 'app-show-job',
  templateUrl: './show-job.component.html',
  styleUrls: ['./show-job.component.css']
})
export class ShowJobComponent implements OnInit {
  isLoading: boolean;
  id: string;
  site: Site = {
    id: '',
    site_name: '',
    site_address: '',
    site_city: '',
    site_state: '',
    site_zip: '',
    site_phone: '',
    site_email: '',
    site_country: '',
    site_contact_email: '',
    site_contact_first_name: '',
    site_contact_last_name: '',
    site_contact_phone: '',
    site_maintenance_contact: '',
    site_maintenance_email: '',
    site_maintenance_phone: '',
    site_ship_to_address: '',
    site_ship_to_city: '',
    site_ship_to_state: '',
    site_ship_to_zip: '',
    site_ship_to_country: '',
    site_ship_to_phone: '',
    site_ship_to_email: ''
  };
  jobId: string;
  job: Job = {
    id: '',
    job_title:  '',
    job_description: '',
    job_asset: '',
    job_notes: '',
    job_status: '',
    job_type: '',
    job_worker: '',
    site_id: '',
    worker_status: '',
  };
  jobs: Job[];

  constructor(private ss: SiteService, private js: JobService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.isLoading = false;
    console.log(this.isLoading);
    this.jobId = this.route.snapshot.params.id;
    this.id = this.job.site_id;
    this.js.getJob(this.jobId).subscribe(job => {
      this.job = job;
    });
    this.isLoading = true;
    console.log(this.isLoading);
  }

}
